import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-accelerometer',
  templateUrl: './accelerometer.component.html',
  styleUrls: ['./accelerometer.component.css']
})
export class AccelerometerComponent implements OnInit {
  // absolute acceleration
  xAcc: number = 0
  yAcc: number = 0
  totalAcc: number = 0

  xAccBS = new BehaviorSubject<number>(0);

  consistentDecceleration: number = 0

  stopsLeft: number = 3;

  constructor() {
  }

  get xAcceleration(){
    return this.xAcc
  }

  get yAcceleration(){
    return this.yAcc
  }
  
  ngOnInit() {

    this.xAccBS.next(1)
    console.log(this.xAccBS)
    
    console.log('Engage');
    window.addEventListener('devicemotion', motion, false);

    let lastX, lastY, lastZ;
    let moveCounter = 0;

    function motion(e: DeviceMotionEvent) {
      let acc = e.acceleration;
      // if (!acc.hasOwnProperty('x')) {
      //   acc = e.accelerationIncludingGravity;
      // }

      this.xAcc = acc.x
      this.yAcc = acc.y
      console.log(this.xAccBS)
      console.log("x: ", this.xAcc)
      console.log("y: ", this.yAcc)
      

      // hypotenuse
      this.totalAcc = this.xAcc + this.yAcc
      console.log("total: ", this.totalAcc)

      // gradual decceleration before eventual stop
      if (this.totalAcc < 0){
        this.consistentDecceleration += 1
      }
      else{
        this.consistentDecceleration = 0
      }

      // eventual stop ie 0 accleration
      if (this.consistentDecceleration >= 50 && Math.abs(this.totalAcc)<=0.20){
        this.stopsLeft -= 1
        this.consistentDecceleration = 0
        console.log("stop detected")
      }

      // if (!acc.x) return;

      //only log if x,y,z > 1
      if (Math.abs(acc.x) >= 1 &&
        Math.abs(acc.y) >= 1 &&
        Math.abs(acc.z) >= 1) {
        //console.log('motion', acc);
        if (!lastX) {
          lastX = acc.x;
          lastY = acc.y;
          lastZ = acc.z;
          return;
        }

        let deltaX = Math.abs(acc.x - lastX);
        let deltaY = Math.abs(acc.y - lastY);
        let deltaZ = Math.abs(acc.z - lastZ);

        console.log("delta x: ", deltaX)

       
        if (deltaX + deltaY + deltaZ > 3) {
          moveCounter++;
        } else {
          moveCounter = Math.max(0, --moveCounter);
        }

        if (moveCounter > 2) {
          console.log('SHAKE!!!');
          moveCounter = 0;
        }

        lastX = acc.x;
        lastY = acc.y;
        lastZ = acc.z;

      }
    
    }

    this.xAccBS.next(lastX)
  }
  }
